body{
    text-align: center;
    background-color: rgb(253, 227, 194);
 
}
#heading{
    font-size: 40px;
    color:rgb(103, 7, 112);
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;

}

#count{
    font-size: 23px;
    color:rgb(100, 50, 4);
    text-shadow: 2px 2px 4px #a49f9f;
}
input[type=text] {
    width: 130px;
    box-sizing: border-box;
    border: 2px solid #eb9090;
    border-radius: 4px;
    font-size: 16px;
    background-color:rgb(249, 239, 226);
    background-image: url('https://cdn3.iconfinder.com/data/icons/feather-5/24/search-512.png');
    background-size: contain;
    background-position: 3px 0px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
    transition: width 0.4s ease-in-out;
    
  }
  
  input[type=text]:focus {
    width: 30%;
  }
.Drinks_data{
    display: grid;
    grid-template-columns: 1fr 1fr  1fr ;
    gap: 1rem;
    text-align: center;
  
 
}
.Drinks_data li{
    list-style-type: none;
    color:rgb(100, 50, 4);
    text-shadow: 2px 2px 4px #c1a6a6;
    transition: width 2s;
}
.Drinks_data li img:hover{
   border: 3px solid rgb(103, 7, 112);
}
.Drinks_data li img{
    width: 300px;
    max-width: 400px;
    max-height: 250px;
    border: 3px solid #f7a4a4;
    border-radius: 10%;
   
}
#copy{
    letter-spacing: 2px;
    font-size: 18px;
}
@media screen and (min-width:269px) and (max-width:920px)
{
    .Drinks_data{
        gap:2rem;
        grid-template-columns: 1fr  ;
    }
    .Drinks_data li img{
       max-width:100%;
       height: auto;
       
    }
    input[type=text]:focus {
        width: 20%;
      }
}